<style lang="scss">@import "./css/style.scss";</style>
<template>
    <div id="navList">
        <ToolBar>
            <el-button type="primary" icon="el-icon-plus" size="small" @click="editNav(false,null)">新增导航</el-button>
            <div>
                <el-input
                        placeholder="请输入导航名称"
                        size="small"
                        style="width: 140px"
                        v-model="searchParams.postTitle"
                        clearable>
                </el-input>
                <el-select v-model="searchParams.postStatus" size="small" clearable placeholder="显示状态"
                           style="width: 120px">
                    <el-option
                            v-for="(v,k) in $Cfg.postStatus"
                            :key="k"
                            :label="v"
                            :value="k">
                    </el-option>
                </el-select>
                <Operator v-model="searchParams.operator" style="width: 120px"></Operator>
                <el-button type="success" size="small" @click="$refs.pagination.Refresh()">查询</el-button>
                <el-button type="warning" size="small" @click="clearSearchParams()">重置</el-button>
            </div>
        </ToolBar>
        <el-table
                :data="tableData"
                border
                style="width: 100%">
            <el-table-column
                    prop="date"
                    label="ID"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="name"
                    label="导航名称"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="address"
                    label="显示状态">
            </el-table-column>
            <el-table-column
                    prop="address"
                    label="创建时间">
            </el-table-column>
            <el-table-column
                    prop="address"
                    label="最近操作人">
            </el-table-column>
            <el-table-column
                    label="操作"
                    width="210">
                <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row)" type="warning" size="small"
                               >禁用</el-button>
                    <el-button @click="editNav(true,scope.row)" type="primary" size="small"
                               >修改</el-button>
                    <el-button @click="handleClick(scope.row)" type="danger" size="small"
                               >删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <Pagination
            ref="pagination"
            :params="searchParams"
            :requestFunc="requestFunc"
            @returnData="returnData"
        />
    </div>
</template>

<script>
    import ToolBar from '@/components/ToolBar.vue';
    import HelpHint from '@/components/HelpHint.vue';
    import Pagination from '@/components/Pagination.vue';
    import paginationMixin from './mixin/page.js'
    import G from 'lodash/get'
    export default {
        mixins:[paginationMixin],
        data() {
            return {

            }
        },
        methods: {
            editNav(isEdit,row){

                this.$prompt('请输入导航名称', isEdit ? '修改导航':'新增导航', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    inputValue:isEdit ? G(row,'b','') : '',
                    inputValidator: v=>{return (v||'').length <=8 && (v||'').length >=1},
                    inputErrorMessage: '不能为空或不能超过8位字符'
                }).then(({ value }) => {
                    this.$message({
                        type: 'success',
                        message: '你的邮箱是: ' + value
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '取消输入'
                    });
                });
            }
        },
        created() {

        },
        components: {
            ToolBar, HelpHint, Pagination
        }
    }
</script>
